import React from 'react';
import {useAuthStateManager} from '../../hooks/useAuthStateManager';
import {
    Container,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardSubtitle,
    CardText,
    Button
} from 'reactstrap';
import {Link} from 'react-router-dom';
import {BaseAuthenticatedState} from '../../types';

const ProfilePage: React.FC = () => {
    const authStateManager = useAuthStateManager();
    const {authData} = authStateManager;
    const {given_name, family_name, email, can_change_password} =
        authData as BaseAuthenticatedState;

    return (
        <Container className="mt-4">
            <Row>
                <Col md={{size: 6, offset: 3}}>
                    <Card>
                        <CardBody>
                            <CardTitle tag="h2">Profile</CardTitle>
                            <CardSubtitle className="text-muted mb-3">
                                {email}
                            </CardSubtitle>

                            <CardText>
                                <strong>Name:</strong> {given_name}{' '}
                                {family_name}
                            </CardText>

                            {can_change_password && (
                                <Link to="/change-password">
                                    <Button color="primary">
                                        Change Password
                                    </Button>
                                </Link>
                            )}
                        </CardBody>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};

export default ProfilePage;
